import { notFound } from 'next/navigation'
import { getProductById, getRelatedProducts } from '@/lib/data'
import { formatPrice } from '@/lib/utils'
import Image from 'next/image'
import Link from 'next/link'
import AddToCartButton from '@/components/AddToCartButton'
import ProductCard from '@/components/ProductCard'

interface ProductPageProps {
  params: {
    id: string
  }
}

export default function ProductPage({ params }: ProductPageProps) {
  const product = getProductById(params.id)
  const relatedProducts = getRelatedProducts(params.id, 4)
  
  if (!product) {
    notFound()
  }
  
  return (
    <div className="min-h-screen dark-theme-bg">
      {/* Header - Dark Theme Design Guidelines */}
      <header className="shadow-lg border-b dark-theme-header">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex items-center justify-between h-16">
            <Link href="/" className="text-2xl font-bold transition-colors hover:text-red-400 dark-theme-text-primary">
              TechStore
            </Link>
            <div className="flex items-center space-x-6">
              <Link 
                href="/"
                className="text-sm font-medium transition-colors flex items-center gap-2 px-4 py-2 rounded-lg hover:bg-gray-700/50 dark-theme-text-secondary"
              >
                <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M10 19l-7-7m0 0l7-7m-7 7h18" />
                </svg>
                Back to Products
              </Link>
            </div>
          </div>
        </div>
      </header>

      {/* Navigation Breadcrumb - Dark Theme Design Guidelines */}
      <nav className="border-b dark-theme-bg dark-theme-border">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex items-center h-12 text-sm">
            <Link href="/" className="transition-colors dark-theme-text-muted">
              Home
            </Link>
            <span className="mx-2 dark-theme-text-muted">/</span>
            <Link href="/" className="transition-colors dark-theme-text-muted">
              Products
            </Link>
            <span className="mx-2 dark-theme-text-muted">/</span>
            <span className="font-medium dark-theme-text-accent">{product.name}</span>
          </div>
        </div>
      </nav>

      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="rounded-2xl overflow-hidden shadow-2xl dark-theme-card">
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-8 p-8">
            {/* Product Image Container - Dark Theme Guidelines */}
            <div className="rounded-xl p-8 aspect-square relative shadow-inner dark-theme-surface">
              <Image
                src={product.image}
                alt={`${product.name} product image`}
                fill
                className="object-contain drop-shadow-lg"
                sizes="(max-width: 1024px) 100vw, 50vw"
              />
            </div>
            
            {/* Product Details - Dark Theme */}
            <div className="space-y-6">
              <div>
                {/* H1 Page Title - Dark Theme Typography */}
                <h1 className="mb-4 text-3xl font-bold leading-tight dark-theme-text-primary">
                  {product.name}
                </h1>
                {/* Product Description */}
                <p className="leading-relaxed text-base dark-theme-text-secondary">
                  {product.description}
                </p>
              </div>
              
              {/* Rating - Dark Theme Design Guidelines */}
              <div className="flex items-center space-x-4 p-4 rounded-xl" style={{ backgroundColor: '#2A2A2A', border: '1px solid #404040' }}>
                <div className="flex items-center">
                  {[...Array(5)].map((_, i) => (
                    <span 
                      key={i} 
                      className={`text-xl drop-shadow-sm ${
                        i < Math.floor(product.rating.average) ? 'text-yellow-400' : 'text-gray-500'
                      }`}
                    >
                      ★
                    </span>
                  ))}
                </div>
                <span className="text-sm" style={{ color: '#CCCCCC' }}>
                  ({product.rating.average.toFixed(1)}) · {product.rating.count} reviews
                </span>
              </div>
              
              {/* Price - Dark Theme Highlight */}
              <div className="p-6 rounded-xl shadow-lg" style={{ backgroundColor: '#2A2A2A', border: '1px solid #FF6B6B' }}>
                <span className="text-3xl font-bold drop-shadow-sm" style={{ color: '#FFFFFF' }}>
                  {formatPrice(product.price)}
                </span>
              </div>
              
              {/* Stock Status & Add to Cart - Dark Theme */}
              <div className="space-y-4 p-6 rounded-xl" style={{ backgroundColor: '#2A2A2A', border: '1px solid #404040' }}>
                <div className="flex items-center justify-between">
                  <span className="font-medium" style={{ color: '#E5E5E5' }}>
                    Availability:
                  </span>
                  <span 
                    className={`px-3 py-1.5 rounded-full text-xs font-semibold shadow-sm`}
                    style={{
                      backgroundColor: product.inStock ? '#4ADE80' : '#FF5252',
                      color: '#FFFFFF',
                      border: `1px solid ${product.inStock ? '#4ADE80' : '#FF5252'}`
                    }}
                  >
                    {product.inStock ? 'In Stock' : 'Out of Stock'}
                  </span>
                </div>
                
                <AddToCartButton
                  productId={product.id}
                  productName={product.name}
                  inStock={product.inStock}
                />
              </div>
              
              {/* Product Details Information Panel - Dark Theme */}
              <div className="p-6 rounded-xl" style={{ backgroundColor: '#2A2A2A', border: '1px solid #404040' }}>
                <h3 className="mb-4 text-lg font-semibold" style={{ color: '#FFFFFF' }}>
                  Product Details
                </h3>
                <dl className="space-y-3">
                  <div className="flex justify-between items-center py-2 border-b" style={{ borderColor: '#404040' }}>
                    <dt className="text-sm" style={{ color: '#CCCCCC' }}>Category:</dt>
                    <dd className="capitalize font-medium" style={{ color: '#E5E5E5' }}>
                      {product.categoryId}
                    </dd>
                  </div>
                  <div className="flex justify-between items-center py-2">
                    <dt className="text-sm" style={{ color: '#CCCCCC' }}>Product ID:</dt>
                    <dd className="font-mono text-sm" style={{ color: '#E5E5E5' }}>
                      {product.id}
                    </dd>
                  </div>
                </dl>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}
